import React from 'react';
import {
  isMobile,
  isTablet,
  isBrowser,
  isDesktop,
  deviceType,
  browserName,
  engineName,
  osName,
  osVersion,
} from 'react-device-detect';

const DeviceInfo = () => {
  const screenWidth = window.screen.width;
  const screenHeight = window.screen.height;
  const supportsSpeech = window.SpeechRecognition || window.webkitSpeechRecognition;

  return (

    <div>
      <h3>Tietoja laitteestasi:</h3>
      Laitetyyppi: {deviceType}<br />
      Selain: {browserName}<br />
      Nytn leveys: {screenWidth}px<br />
      Nytn korkeus: {screenHeight}px<br />
      Puheentunnistus: {supportsSpeech ? 'Kyll' : 'Ei'}<br />
      Onko selain: {isBrowser ? 'Kyll' : 'Ei'}<br />
      Onko mobiililaite: {isMobile ? 'Kyll' : 'Ei'}<br />
      Onko tabletti: {isTablet ? 'Kyll' : 'Ei'}<br />
      Onko pytkone: {isDesktop ? 'Kyll' : 'Ei'}<br />
      Moottori: {engineName}<br />
      Kyttjrjestelm: {osName}<br />
      Kyttjrjestelmn versio: {'>='} {osVersion}<br />
      Katso versio laitteen tiedoista tarkemmin.
      {/*
            Kyttjrjestelmn versio: {getOSVersion()}<br/>
            Kyttjrjestelm: {getOperatingSystem()}
             */}

    </div>

  );
};

export default DeviceInfo;
