import React, { useCallback, createContext, useState, useContext, useEffect } from 'react';
import { upsertColorDefinition as controllerUpsertColorDefinition, getColorDefinition as controllerGetColorDefinition } from './controller';

// Vriobjekti, jossa vrit mritelln vaiheilla
const colors = {
    c1: { code: '#f5ec42', name: 'Yellow' },
    c2: { code: '#369e3b', name: 'Green' },
    c3: { code: '#db1616', name: 'Red' },
    /*
    c4: { code: '#00c3ff', name: 'Blue' },
    grey: { code: '#929596', name: 'Grey' },
    */
    black: { code: '#000', name: 'Black' },
};

const noColor = { code: '#e1f5eb', name: 'NoColor' }; // Taustan vrinen

// Vri konteksti
const ColorContext = createContext();

// Vri kontekstin tarjoaja
export const ColorProvider = ({ children }) => {
    // Alkuperiset valitut vrit, joissa jokainen vaihe on taulukko valituista vreist
    const [selectedColors, setSelectedColors] = useState([]);
    const [colorDefinitions, setColorDefinitions] = useState({});
    const [errorState, setErrorState] = useState(null);

    // Funktio kaikkien vrien tilan resetointiin
    const resetColors = () => {
        setSelectedColors([]); // Tyhjenn valitut vrit
        setColorDefinitions({}); // Tyhjenn vrimrittelyt        
    };

    const toggleColor = (color) => {
        setSelectedColors(prevColors =>
            prevColors.includes(color)
                ? prevColors.filter(c => c !== color)
                : [...prevColors, color]
        );
    };


    // Funktio vrin lismreen asettamiseen ja tallentamiseen
    const setColorDefinition = async (colorId, definition) => {
        try {
            // Pivit paikallinen tila
            setColorDefinitions(prevDefinitions => ({
                ...prevDefinitions,
                [colorId]: definition
            }));

            // Lis tai pivit IndexedDB:ss kyttmll controllerin funktiota
            await controllerUpsertColorDefinition(colorId, definition);
        } catch (error) {
            console.error('Virhe tallennettaessa vrin mrittely:', error);
            //setErrorState(`Virhe tallennettaessa vri ${colorId}: ${error.message}`);            
            throw new Error('Virhe tallennettaessa vrin mrittely: ' + error);
        }
    };


    // Funktio kaikkien vrimrittelyjen lataamiseen
    //const loadColorDefinitions = async () => {
    const loadColorDefinitions = useCallback(async () => {
        try {
            console.log('Ladataan vrimrittelyt...');
            for (const colorId of Object.keys(colors)) {
                await loadColorDefinition(colorId); // Lataa vrin mrittely
            }
            console.log('Vrimrittelyt ladattu.');
        } catch (error) {
            console.error('Virhe vrimrittelyjen latauksessa:', error);
        }
        //};
    }, []); // EI list colors-riippuvuutta, koska colors ei muutu, se on vakio. Tyhj taulukko kuitenkin listty

    // Funktio haetaan vrin lismre, jos sellainen on tallennettu
    const loadColorDefinition = async (colorId) => {
        try {
            const definition = await controllerGetColorDefinition(colorId);
            if (definition) {
                setColorDefinitions(prevDefinitions => ({
                    ...prevDefinitions,
                    [colorId]: definition
                }));
            }
            
        } catch (error) {
            console.error('Virhe haettaessa vrin mrittely:', error);
            setErrorState(`Virhe haettaessa vrin mrittely ${colorId}: ${error.message}`);
            //throw new Error('Virhe haettaessa vrin mrittely: ' + error);

        }

    };


    //TODO, ei ladata tss
    /*
    useEffect-hookin riippuvuustaulukossa pitisi olla kaikki hookin sisll kytettvt muuttujat ja funktiot, jotka ovat komponentin ulkopuolella tai uudelleenluotavia. Tss tapauksessa loadColorDefinitions on mritelty komponentin sisll.
    loadColorDefinitions-funktio luodaan joka kerta, kun komponentti renderidn. useEffect ei tied, onko funktio muuttunut, jos sit ei ole listty riippuvuustaulukkoon.
    Huom! kytettv useCallback loadColorDefinitions:ssa.  Sinne EI list colors-riippuvuutta, koska colors ei muutu, se on vakio
    */

    useEffect(() => {
        loadColorDefinitions();
    }, [loadColorDefinitions]);


    return (
        <ColorContext.Provider value={{
            colors,
            selectedColors,
            toggleColor,
            setSelectedColors,
            setColorDefinition,
            colorDefinitions,
            resetColors,
            loadColorDefinitions,
            noColor,
            errorState
        }}>
            {children}
        </ColorContext.Provider>
    );
};

// Vri kontekstin kytt
export const useColors = () => useContext(ColorContext);
